import Endpoint from '@/services/Endpoint'

export default {
  chequeStatuses (payload) {
    return Endpoint().get('/s1/cheque/chequeStatus', {
      params: payload
    })
  },
  createChequeStatus (payload) {
    return Endpoint().post('/s1/cheque/chequeStatus', payload)
  },
  modifyChequeStatus (payload) {
    return Endpoint().patch('/s1/cheque/chequeStatus', payload)
  }
}
